<template class="student_list">
  <div>
    <Form inline>
      <FormItem>
        <Input placeholder="Enter something..." clearable />
      </FormItem>
      <FormItem>
        <Select clearable style="width: 150px" placeholder="请选择学生类型">
        </Select>
      </FormItem>
      <FormItem>
        <Select clearable style="width: 150px" placeholder="请选择学生学制">
        </Select>
      </FormItem>
      <FormItem>
        <Select clearable style="width: 150px" placeholder="请选择学生年级">
        </Select>
      </FormItem>
      <FormItem>
        <DatePicker
          type="daterange"
          split-panels
          placeholder="开始时间 ~ 结束时间"
          style="width: 200px"
        ></DatePicker>
      </FormItem>
      <FormItem>
        <Button type="primary" icon="ios-search">Search</Button>
      </FormItem>
      <FormItem>
        <Button type="primary" icon="ios-search">数据导入</Button>
      </FormItem>
      <FormItem>
        <Button type="primary" icon="ios-search">数据导出</Button>
      </FormItem>
    </Form>
    <!--h1>设计一：每个学生的信息放在一块卡片上，可以展示出更多信息</h1-->
    <div v-for="student in students" :key="student.id" class="stuCard">
      <Card :key="student.id">
        <Row>
          <Col span="2">
                <Button>修改信息</Button>
          </Col>
          <Col span="4">
            <Row>
              <Col>
                <Avatar size="large">{{ student.name }}</Avatar>
              </Col>
              <Col>
                <p>姓名:<b>{{ student.name }}</b></p>

                <p>学号:{{ student.id }}</p>
                <p> 类型:{{ student.stu_type }}</p>
                <p> 学制:{{student.study_year}}</p>
              </Col>
              <div class="avatar"></div>
            </Row>
          </Col>
          <Col span="18">
            <mySteps :steps="student.steps"></mySteps>
          </Col>
        </Row>
        <br>
        <Collapse simple>
          <Panel name="1">
            <b>学生成果列表</b>
            <div slot="content">
              <Table :columns="achievement_table" :data="student.achievements">
                <template slot-scope="{ row }" slot="attach_file">
                  <Icon type="ios-cloud-download-outline" size="24" />
                  <a>{{ row.attach_file }}</a>
                </template>
              </Table>
            </div>
          </Panel>
        </Collapse>
      </Card>
      <br />
    </div>
    <br />
    <!--h1>设计二：学生信息由表格展示，更为紧凑</h1>
    <Table :columns="stu_info" :data="students"></Table-->
    <p>疑问一：培养流程不是完全线性的流程，如何处理哪些非线性的步骤</p>
    <p></p>
    <Modal>
      <Form>
        <FormItem label="学生姓名">
          <Input></Input>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>
