<style scoped>
.layout {
  border: 1px solid #d7dde4;
  background: #f5f7f9;
  position: relative;
  border-radius: 4px;
  overflow: hidden;
  min-height: 100%;
}
.layout-logo {
  width: 100px;
  height: 30px;
  border-radius: 3px;
  float: left;
  position: relative;
  top: 15px;
  left: 20px;
}
.layout-nav {
  width: 420px;
  margin: 0 auto;
  margin-right: 20px;
}
.layout-footer-center {
  text-align: center;
}
.home-header {
  background-color: white;
}
.student-content {
  margin: 50px 8% 0;
  min-height: 80%;
  align-content: center;
}
.menu-item {
    display: flex;

  justify-items: center;
  align-items: center;
}
</style>
