                <Button shape="circle">课程修读</Button>
              </div>
            </Col>
            <Col span="6">
              <div class="menu-item">
                <Icon type="ios-document-outline" size="50" />
                <br />
                <Button shape="circle">年度报告</Button>
              </div>
            </Col>
            <Col span="6">
              <div class="menu-item">
                <Icon type="ios-create-outline" size="50" />
