                <Button shape="circle">开题报告</Button>
              </div>
            </Col>
          </Row>
          <Row :gutter="16">
            <Col span="6">
              <div class="menu-item">
                <Icon type="ios-checkbox-outline" size="50" />
                <br />
                <Button shape="circle">中期审核</Button>
              </div>
            </Col>
            <Col span="6">
              <div class="menu-item">
                <Icon type="ios-copy-outline" size="50" />
                <br />
                <Button shape="circle">科研成果审核</Button>
              </div>
            </Col>
            <Col span="6">
              <div class="menu-item">
                <Icon type="ios-cube-outline" size="50" />
