                <Button shape="circle">盲审</Button>
              </div>
            </Col>
            <Col span="6">
              <div class="menu-item">
                <Icon type="ios-school-outline" size="50" /><Icon type="" />
                <br />
                <Button shape="circle">毕业论文存档</Button>
              </div>
            </Col>
          </Row>
        </Card>
      </div>
      <Footer class="layout-footer-center">2021-2022 &copy; 华东师范大学计算机科学与技术学院</Footer>
