          <Icon type="ios-thumbs-up-outline" />
        </Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import Header from "../components/Header";
export default {
  components: {
    "my-header": Header,
  },
  data(){
      return{
          modalShow:false
      }
  },
  mounted() {
      this.modalShow=true;
  },
  methods:{
      modalShowChange(){
          this.modalShow=false;
      }
  }
};
</script>
