<template>
  <div>
    <div class="menu-log">
      <img alt="logo" src="../assets/logo.svg" class="logo">
      <h4 v-if="!isCollapsed">研究生管理系统</h4>
      <h5
        v-if="!isCollapsed"
        style="color:#57a3f3"
      >版本：V0.0.2</h5>
    </div>
    <Sider
      ref="side1"
      hide-trigger
      collapsible
      :collapsed-width="80"
      v-model="isCollapsed"
      style="height:100%;"
    >
      <Menu
        :active-name="menuName"
        class="my-menu"
        @on-select="toRouter"
        :class="menuitemClasses"
        width="auto"
      >
        <MenuItem name="studentList">
        <Icon type="ios-podium" />
        <span>学生列表</span>
        </MenuItem>
        <MenuItem name="teacherList">
        <Icon type="logo-buffer" />
        <span>导师列表</span>
        </MenuItem>
        <MenuItem name="infoList">
        <Icon type="ios-navigate"></Icon>
        <span>通知发布</span>
        </MenuItem>
        <MenuItem name="studentFlow">
        <Icon type="ios-book" />
        <span>培养流程</span>
        </MenuItem>
      </Menu>
    </Sider>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  name: "my-sider",
  data() {
    return {
      menuName: "operationalData",
      isCollapsed: false,
    };
  },
  computed: {
    ...mapState(["avatarSize"]),
    menuitemClasses() {
      return ["menu-item", this.isCollapsed ? "collapsed-menu" : ""];
    },
  },
  mounted() {
    this.menuName = this.$route.name;
  },
  watch: {
    $route: function (to) {
      this.menuName = to.name;
    },
  },
  methods: {
    toRouter(name) {
      this.$router.push(name);
    },
    collapsedSider() {
      this.$refs.side1.toggleCollapse();
    },
  },
};
</script>
<style scoped>
.menu-item span {
  display: inline-block;
  overflow: hidden;
  width: 69px;
  text-overflow: ellipsis;
  white-space: nowrap;
  vertical-align: bottom;
  transition: width 0.2s ease 0.2s;
}
.menu-item i {
  transform: translateX(0px);
  transition: font-size 0.2s ease, transform 0.2s ease;
  vertical-align: middle;
  font-size: 16px;
}
.collapsed-menu span {
  width: 0px;
  transition: width 0.2s ease;
}
.collapsed-menu i {
  transform: translateX(5px);
  transition: font-size 0.2s ease 0.2s, transform 0.2s ease 0.2s;
  vertical-align: middle;
  font-size: 22px;
}
</style>